export default "*{\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n#captchaBox {\r\n    width: 100%;\r\n    height: 100vh;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    box-sizing: border-box;\r\n    flex-direction: column;\r\n    position: relative;\r\n    background-color: rgba(0, 0, 0, 0.3);\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n\r\n#captcha {\r\n    display: block;\r\n    border-radius: 4px;\r\n    background-size: cover;\r\n    background-position: center;\r\n    position: relative;\r\n    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);\r\n}\r\n.bgImg{\r\n    position: absolute;\r\n    top:0;\r\n    left:0;\r\n    z-index: 1;\r\n    user-select:none;\r\n}\r\n\r\n#sliderImg {\r\n    position: absolute;\r\n    top: 0px;\r\n    user-select: none;\r\n    background-size: cover;\r\n    background-position: center;\r\n    will-change: transform;\r\n    z-index: 10;\r\n    user-select:none;\r\n}\r\n\r\n#sliderImg > img {\r\n    position:absolute;\r\n    top: 0;\r\n    left: 0;\r\n    user-select:none;\r\n}\r\n\r\n.border {\r\n    position: absolute;\r\n    margin: 0;\r\n    border-radius: 6px;\r\n    box-sizing: border-box;\r\n    box-shadow: inset 0px 0px 7px 1px #fff;\r\n}\r\n\r\n#opacityImg {\r\n    position: absolute;\r\n    border-radius: 6px;\r\n    background-color: rgba(0, 0, 0, 0.4);\r\n    background-blend-mode: multiply;\r\n    border: 1px solid #fff;\r\n    z-index:3;\r\n    user-select:none;\r\n}\r\n\r\n.slide {\r\n    height: 45px;\r\n    border-radius: 2px;\r\n    background-color: #eee;\r\n    position: absolute;\r\n    left: 0;\r\n    bottom: -55px;\r\n    vertical-align: top;\r\n}\r\n\r\n.btn {\r\n    display: block;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: inherit;\r\n    border-radius: inherit;\r\n    background-color: white;\r\n    box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.25), 0 2px 4px rgba(0, 0, 0, 0.3);\r\n    position: absolute;\r\n    cursor: move;\r\n    transition: 0.25s all ease-in-out;\r\n}\r\n\r\n#captcha:active .slide .btn {\r\n    transition: none;\r\n}\r\n\r\n#captcha.pass::before,\r\n#captcha.pass::after,\r\n#captcha.pass>.slide {\r\n    display: none;\r\n}\r\n\r\n#opacityImg.pass {\r\n    display: none;\r\n}\r\n\r\n#sliderImg.pass {\r\n    display: none;\r\n}\r\n\r\n#refresh {\r\n    position: absolute;\r\n    top: 5px;\r\n    right: 5px;\r\n    cursor: pointer;\r\n    z-index: 11;\r\n}"